  justify-content: center;
}

.register__form--container {
  width: 80%;
  border-radius: 30px;
  background: #d2d0d0;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 20px 20px 60px #595858, -20px -20px 60px #ffffff;
}

.register__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 24px;
}

.register__form--left {
  padding: 60px 24px;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 2px solid black;
}
.register__form--right {
  padding: 24px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.register__title {
  font-size: 22px;
  text-align: center;
}

.btn__register {
  background-color: rgb(51, 51, 151);
  border: none;
  font-size: 24px;
  color: white;
  padding: 12px 24px;
  margin-top: 24px;
  border-radius: 20px;
  cursor: pointer;
}

.detail__label {
  font-size: 24px;
  margin-top: 12px;
}

select {
  border: none;
  padding: 12px;
  border-radius: 20px;
  margin-top: 12px;
  width: 50%;
}

.quick__title {
  font-size: 32px;
  text-align: center;
  font-weight: 700;
}

.quick__register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
}

.quick__icons {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.quick__icon {
  height: 40px;
  width: 40px;
}

.registration__input {
  width: 70%;
  margin: 16px 24px;
  padding: 12px 24px;
  text-align: center;
  border: 1px solid #14143c;
  border-radius: 4px;
  background: #d6d1d1;
  box-shadow: 5px 5px 10px #9c9999, -5px -5px 10px #ffffff;
}

.registration__button {
  cursor: pointer;
  padding: 12px 24px;
  margin-top: 20px;
  width: 30%;
  border: 1px solid #14143c;
  border-radius: 4px;
  background: #d6d1d1;
  box-shadow: 5px 5px 10px #9c9999, -5px -5px 10px #ffffff;
}

/*

DASHBOARD

*/

.container__dash {
  padding: 20px 0;
  min-height: 70vh;
}

.menu {
  width: 25%;
  display: flex;
  flex-direction: column;
}

.menu__user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 12px 0;
}

.menu__icon {
  width: 30%;
}

.menu__para {
  margin-top: 8px;
  font-size: 16px;
}

.menu__title {
  font-size: 20px;
  margin: 6px 0;
  font-weight: 700;
}

.menu__items {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;
}

.menu__item {
  margin-bottom: 20px;
  font-size: 18px;
  list-style-type: none;
  cursor: pointer;
}

.menu__item:hover {
  color: rgb(51, 51, 151);
}

.menu__fonticon {
  margin-right: 12px;
}

.menu__low {
  margin-top: 60px;
  font-size: 14px;
  color: gray;
}

.statistics {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.summary {
  width: 98%;
  height: 20%;
