}

.todo__list {
  margin: 12px 0;
  width: 95%;
  border-radius: 20px;
  background: rgba(214, 209, 209, 0.75);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.2);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.8);
