  cursor: pointer;
}

.plans__footer {
  width: 100%;
  margin: 12px 0;
  padding: 0 12px;
  display: flex;
  justify-content: space-between;
}

.plan__title--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.plan__icon {
  margin-right: 12px;
}

.plan__title {
  font-size: 14px;
}

.todo__smalltitle {
  background-color: black;
  padding: 8px;
  border-radius: 12px;
  transition: all 300ms ease;
}

.plan:hover .todo__smalltitle {
  background-color: white;
  color: black;
}

.plan__description {
  margin-top: 6px;
}

.plan__details {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.plan__detail {
  width: 45%;
  display: flex;
  margin: 12px 0px;
  align-items: center;
}

