  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.yt__video {
  margin-top: 20px;
}

.video__title--container {
  display: flex;
  width: 100%;
  padding: 0px 36px;
  justify-content: space-between;
}

.video__title--left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.video__title--checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*

TODO

*/

.todo__desc {
  margin: 6px 0;
}

.todo__header {
  padding: 12px;
  display: flex;
  justify-content: space-between;
}

.todo__btn {
  padding: 6px;
  border-radius: 12px;
  cursor: pointer;
}

.todo__add {
  cursor: pointer;
  transition: all 300ms ease;
}

.todo__add:hover {
  color: rgb(207, 204, 204);
