/* MODAL */

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #3737a2;
  padding: 30px;
  z-index: 1000;
  border-radius: 10%;
}

.modal__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  padding: 30px;
  border-radius: 20px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.todo__input {
  margin: 4px 0 12px 0;
  padding: 6px 48px;
}

.modal__label {
  color: white;
  font-size: 18px;
}

.todo__select {
  width: 250px;
  margin: 12px 0;
}

.modal__close {
  margin-bottom: 12px;
  color: white;
  font-size: 24px;
  cursor: pointer;
}

/* MEAL */

.meal__title--container {
  display: flex;
  justify-content: space-between;
  padding: 12px;
  border-bottom: 1px solid white;
}

.meal__icon {
  font-size: 32px;
  margin-right: 12px;
}
.meal__main--container {
  height: 100%;
  display: flex;
}

.meal__add {
  padding: 12px;
  width: calc(100% / 3);
  height: 100%;
  border-right: 1px solid white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.meal__list {
  justify-content: flex-start;
}

.meal__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.meal__subtitle {
  border-bottom: 1px solid white;
  margin-bottom: 20px;
}
.meal__btn {
  margin-top: 30px;
  padding: 6px 20px;
  border-radius: 20px;
}

.meal__input {
  margin-bottom: 12px;
}

.meal {
  width: 100%;
  height: 25%;
  border: 1px solid white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.meal__data {
  margin-top: 12px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.all__meal {
  width: 100%;
  height: 400px;
  overflow: scroll;
  overflow-x: hidden;
}

.meal__icon {
  font-size: 24px;
  cursor: pointer;
}

.meal__cancel {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.meal__statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.statistic__number {
  font-size: 48px;
}

.statistic__subtitle {
  background-color: white;
  color: black;
  padding: 6px 12px;
  border-radius: 20px;
}

/*MEDIA */

@media (max-width: 720px) {
  .container {
    padding: 28px 0;
  }

  .row__column {
    width: 90%;
  }
  .logo__text {
    font-size: 20px;
  }

  .nav__item {
    font-size: 12px;
  }
  .area__title {
    font-size: 50px;
  }
  .feature {
    width: 50%;
  }
  .features__para {
    font-size: 14px;
  }
  .steps__textarea {
    width: 90%;
  }

  .steps__img--container {
    width: 0%;
  }
  .row {
    align-items: center;
    justify-content: center;
  }
  .footer__logo {
    font-size: 38px;
  }
  .footer__rights {
    font-size: 12px;
  }
  .btn {
    font-size: 18px;
    padding: 18px;
  }
  .menu__item {
    font-size: 14px;
  }
}

@media (max-width: 1000px) {
  .area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .login__image {
    width: 0%;
  }
  .login__form--container {
    width: 100%;
  }
  .area__title {
    text-align: center;
  }
  .area__right {
    width: 0%;
  }
  .area__img {
    display: none;
  }
  .area__left {
    width: 90%;
    justify-content: center;
    align-items: center;
  }
  .area__description {
    font-size: 18px;
    text-align: center;
  }
  .feature__title {
    text-align: center;
  }
  .dash__row {
    flex-direction: column;
  }
  .menu {
    flex-direction: row;
    width: 100%;
  }
  .menu__items {
    width: 100%;
  }
  .menu__items--list {
    display: flex;
    flex-wrap: wrap;
  }
  .menu__item {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0;
    margin: 12px 0;
  }

  .menu__low {
    margin-top: 20px;
  }
}

@media (max-width: 1200px) {
  .nav__container {
    width: 90%;
  }
  .row {
    width: 90%;
  }
  .scroll__icon {
    display: none;
  }
  .container,
  .container__dash {
    min-height: 90vh;
  }
  .plan {
    width: 45%;
  }
  .statistic__number {
    font-size: 32px;
  }
  .statistic__subtitle {
    font-size: 16px;
  }
}
